<!--  -->
<template>
    <div>
        <nav-bar>
            <div slot="left" class="left" @click="leftClick">
                <img src="~assets/image/common/left.png" alt="">
            </div>
            <div slot="center">
                <tab-control :tabList="tabList" :is-bottom="false" @handleTabClick="handleTabClick"  ref="nav"></tab-control>
            </div>
        </nav-bar>
    </div>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'
import TabControl from 'components/content/tabControl/TabControl'
export default {
    name: 'DatailNavBar',
    components: {
        NavBar,
        TabControl
    },
    data() {
        return {
            tabList:[
                {text: '商品'},
                {text: '参数'},
                {text: '评价'},
                {text: '推荐'}
            ],
            currentIndex: 0
        };
    },
    //方法集合
    methods: {
        leftClick(){
            this.$router.go(-1)
        },
        handleTabClick(index){
            this.$emit('handleTabClick', index)
        }
    },
}
</script>
<style scoped>
.left{
    box-sizing: border-box;
    padding: 10px;
}
.left img{
    width: 100%;
}
</style>