import {req} from './req'

export function getDetail(goods_id){
    return req({
        url: '/goods/detail',
        params: {
            goods_id
        }
    })
}
export function getDetailGoods(cid) {
    return req({
        url: '/goods/search',
        params: {
            cid,
            pagesize: 8
        }
    })
}