<!--  -->
<template>
    <div id="detail">
        <detail-nav-bar class="navBar" @handleTabClick="handleTabClick" ref="detailnav"/>
        <scroll class="scroll_content" 
                ref="scroll"
                :probe-type="3"
                @backTopScroll="positionScroll">
            <detail-swiper :banners="banners"/>
            <detail-base-info :good-info="goodInfo" ref="baseInfo"/>
            <detail-good-info :introduce = 'goods_introduce' ref="parmas"/>
            <div  ref="comment"><h1>评论</h1></div>
            <good-list :goods="goodsList" ref="recommend"/>
        </scroll>
        <detail-bottom @addCart="addToCart" @buyClick="buyClick"></detail-bottom>  
        <back-top @click.native="backClick" v-show="isBackTop" />
    </div>
</template>

<script>
import Scroll from 'components/common/scroll/Scroll'

import DetailNavBar from './childComps/DetailNavBar'
import DetailSwiper from './childComps/DetailSwiper'
import DetailBaseInfo from './childComps/DetailBaseInfo'
import DetailGoodInfo from './childComps/DetailGoodInfo'
import DetailBottom from './childComps/DetailBottom'

import GoodList from 'components/content/goodList/GoodList'

import { getDetail, getDetailGoods } from 'network/detail'
import { itemListenerMixin,backTopMixin }from 'common/mixin'

import { mapActions } from 'vuex'

export default {
    name: "Detail",
    mixins: [itemListenerMixin,backTopMixin],
    components: {
        Scroll,
        DetailNavBar,
        DetailSwiper,
        DetailBaseInfo,
        DetailGoodInfo,
        GoodList,
        DetailBottom
    },
    data() {
        return {
            goods_id: null,
            banners:[],
            goods_introduce: '',
            goodInfo: {},
            tabOffsetTop:0,
            goodsList:[],
            themeTopYs: [],
            currentIndex: 0,
        };
    },
    created() {
        this.goods_id = this.$route.params.id
        // 获取商品详情
        getDetail(this.goods_id).then(res => {
            this.goods_introduce = res.message.goods_introduce
            this.banners = res.message.pics
            this.goodInfo = res.message
        })
        // 获取推荐信息
        getDetailGoods(this.goodInfo.cat_id).then(res => {
            this.goodsList.push(...res.message.goods);
        })
        this.$nextTick(() => {
            setTimeout(() => {
                this.themeTopYs = [];
                this.themeTopYs.push(0)
                this.themeTopYs.push(this.$refs.parmas.$el.offsetTop)
                this.themeTopYs.push(this.$refs.comment.offsetTop)
                this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
                this.themeTopYs.push(Number.MAX_VALUE)
                console.log(this.themeTopYs)
            }, 700)           
        })

    },
    destroyed() {
        this.$bus.$off('imageLoad', this.itemImageLoad)
    },
    watch: {
        // 方法1 //监听路由是否变化
        $route(to, from) {
            if (to.name == detail) {
            this.init();
            this.$refs.scroll.scrollTo(0, 0,0)
            }
            
        }
    },
    //方法集合
    methods: {
        ...mapActions([
            'addCart'
        ]),
        init(){
            this.goods_id = this.$route.params.id
            // 获取商品详情
            getDetail(this.goods_id).then(res => {
                this.goods_introduce = res.message.goods_introduce
                this.banners = res.message.pics
                this.goodInfo = res.message
            })
            // 获取推荐信息
            getDetailGoods(this.goodInfo.cat_id).then(res => {
                this.goodsList.push(...res.message.goods);
            })
            this.$nextTick(() => {
                setTimeout(() => {
                    this.themeTopYs = [];
                    this.themeTopYs.push(0)
                    this.themeTopYs.push(this.$refs.parmas.$el.offsetTop)
                    this.themeTopYs.push(this.$refs.comment.offsetTop)
                    this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
                    this.themeTopYs.push(Number.MAX_VALUE)
                    console.log(this.themeTopYs)
                }, 700)
            })
        },
        handleTabClick(index){
            this.$refs.scroll.scrollTo(0,-this.themeTopYs[index],200)
        },
        positionScroll(position){
            const positionY = -position.y
            // 监听回到顶部
            this.listenShowBackTop(positionY)
            // 根据滚动位置改变tap
            let length = this.themeTopYs.length;
            
            for (let i = 0; i < length-1; i++) {
                // 普通判断
                // console.log(i == length - 1 && positionY >= this.themeTopYs[i], this.themeTopYs[i], positionY)
                // if (this.currentIndex != i && ((i < length-1 && positionY >= this.themeTopYs[i] && positionY < this.themeTopYs[i+1]) || (i === length - 1 && positionY >= this.themeTopYs[i] ))) {
                //     this.currentIndex = i
                //     this.$refs.detailnav.$refs.nav.currentIndex= this.currentIndex;
                // }
                // hack技巧，数组添加最大值
                if (this.currentIndex != i && (positionY >= this.themeTopYs[i] && positionY < this.themeTopYs[i + 1])) {
                    this.currentIndex = i
                    this.$refs.detailnav.$refs.nav.currentIndex= this.currentIndex;                    
                }
            }
        },
        // 添加商品到购物车
        addToCart(){
            // console.log('添加商品到购物车')
            // 获取购物车需要的信息
            const product = {}
            product.goods_id = this.goodInfo.goods_id
            product.goods_name = this.goodInfo.goods_name
            product.goods_price = this.goodInfo.goods_price
            product.goods_image = this.goodInfo.pics[0].pics_sma_url

            // 将商品添加到购物车
            // this.$store.commit('addCart',product) 
            // this.$store.dispatch('addCart', product).then(res => {
            //     console.log(res)
            // })
            // 弹窗提示
            this.addCart(product).then(res => {
                this.$toast.show(res,2000)
                console.log(this.$toast.isShow);
                
            })
        },
        buyClick(){
            console.log('前往订单页面');
            
        }
    },
}
</script>
<style scoped>
#detail{
    box-sizing: border-box;
    position: relative;
    z-index: 9;
    height: 100vh;
    background-color: #ffffff;
}    
.scroll_content{
    position: absolute;
    left: 0;
    right: 0;
    top: 44px;
    bottom: 50px;
}
.navBar{
    background-color: #ffffff;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
}
</style>